
.section-reviews{
    text-align: center;
padding-bottom: 128px;
padding-top: 80px;
    @media screen and (min-width: 768px) {
padding-bottom: 166px;
padding-top: 121px;
    }
    @media screen and (min-width: 1280px) {
        padding-top: 123px;
        padding-bottom: 179px;
    }
}

.section-reviews__text{
    font-weight: 500;
    font-size: 12px;
    line-height: 2.55;
    color: $txt-black;
    position: relative;
    width: 157px;
    z-index: 3;
    margin: 0;
    background-image: repeating-linear-gradient(to top, #E1E1E1 0px 1px, transparent 1px 31px);
    @media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: 2.16;
        letter-spacing: 0.04em; 
        width: 460px;
    }
    @media screen and (min-width: 1280px) {
        font-size: 16px;
        line-height: 1.86;
        width: 750px;
    }
}
.section-reviews__text-info{
    font-weight: 500;
    font-size: 14px;
    line-height: 1.86;
    color: $txt-black;
    @media screen and (min-width: 768px) {
        font-size: 16px;
    }
    @media screen and (min-width: 1280px) {
        font-size: 20px;
    }

}
.section-reviews__img{
    width: 85px;
    height: 85px;
    margin-left: auto;
    margin-right: auto;
}
.section-reviews__wrap::before {
    content: "";
    position: absolute;
background-image: url(../images/quotation-mark.svg);
    width: 54px;
    height: 40px;
    z-index: 1;
    left: 61px;
    top: 28px;
    @media screen and (min-width: 768px) {
        left: 41px;
        top: 30px;
    }
    @media screen and (min-width: 1280px) {
        left: 58px;
        top: 30px;
    }
   }
   .section-reviews__wrap{
 
    padding: 43px 66px 26px 57px;
    position: relative;
    margin-top: 15px;
    @media screen and (min-width: 768px){
        padding: 47px 80px 34px 80px;
        margin-left: 5%;
    }
    @media screen and (min-width: 1280px){
        padding: 47px 88px 39px 88px;
        margin-left: 5%;
    }
   }
  
